.featured {
    width: 100%;
    position: relative;
    overflow: hidden;
    background-color: $color-bg;

    &__list {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            width: 20%;
            padding: 0 10px;
            display: inline-block;

            img {
                max-width: 100%;
                height: 40px;
                margin: 0 auto;
                display: block;
                @include filter(grayscale(100%));
            }
        }
    }

    @include breakpoint('large') {
        &__list {
            li {
                img {
                    height: 35px;
                }
            }
        }
    }

    @include breakpoint('medium') {
        &__list {
            li {
                width: 25%;

                &:last-child {
                    display: none;
                }
            }
        }
    }

    @include breakpoint('small') {
        &__list {
            li {
                padding: 30px 10px;
                width: 50%;

                &:last-child {
                    display: none;
                }
            }
        }
    }

    @include breakpoint('xs') {
        &__list {
            li {
                padding-top: 20px;
                padding-bottom: 20px;
                width: 100%;

                &:last-child {
                    display: block;
                }
            }
        }
    }
}
