.item {
    border-top: 1px dashed lighten($color-secondary, 13%);
    padding-top: 77px;
    margin: 77px 0;
    color: $color-bg;
    @include display(flex);
    @include align-items(center);
    @include justify-content(space-between);

    &:first-child {
        margin-top: 0;
    }

    &__description {
        position: relative;
        width: calc(47% - 70px);

        h2 {
            margin: 0 0 10px 0;
            display: block;
            line-height: 1.3;
            z-index: 20;
            text-transform: uppercase;
            font-weight: 700;
            color: $color-bg;
            @include rem(font-size, 28px);
        }

        p {
            margin: 35px 0 20px 0;
            opacity: 1;
            line-height: 1.7;
            z-index: 20;
            color: #fff;
            @include rem(font-size, 22px);
        }

        &:before {
            position: absolute;
            top: -140px;
            counter-increment: section;
            font-weight: 700;
            z-index: 10;
            color: rgba($color-bg, 0.3);
            opacity: 0.5;
            content: counter(section, decimal-leading-zero);
            @include rem(font-size, 172px);
        }
    }

    &__image {
        width: 53%;

        img {
            max-width: 100%;
        }
    }

    @include breakpoint('small') {
        display: block;
        padding-top: 70px;
        margin-top: 30px;
        text-align: center;

        &:nth-child(2) {
            @include display(flex);
            @include flex-direction(column-reverse);
        }

        &:last-child {
            padding-bottom: 30px;
        }

        &__description {
            width: 100%;

            &:before {
                display: none;
            }
        }

        &__image {
            width: 70%;
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 20px;
        }
    }
}
