$documentation-menu-width: 300px;
$documentation-background: $color-secondary;
$documentation-arkio-background: #ffcd44;

.documentation {
    &__layout,
    &__menu,
    &__main {
        @include transition(all 0.3s ease);
    }

    &__layout {
        position: relative;
        padding-left: 0;

        &.active .documentation__aside {
            width: $documentation-menu-width;
            @include transform(translateX($documentation-menu-width));
        }

        &.active .documentation__menu-link {
            @include transform(translateX($documentation-menu-width));
        }
    }

    &__aside {
        margin-left: -$documentation-menu-width;
        width: $documentation-menu-width;
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        bottom: 0;
        z-index: 90;
        overflow: hidden;
        transition: transform 0.3s ease;
        background: $documentation-background;
        @include transform(translateX(0px));

        &:before {
            content: "";
            display: block;
            position: absolute;
            left: 0;
            bottom: 80px;
            width: 100%;
            height: 100px;
            z-index: 100;
            background: -webkit-linear-gradient(top,transparent, $documentation-background);
            background: linear-gradient(to bottom,transparent, $documentation-background);
            pointer-events: none;
        }

        .ps-scrollbar-y-rail {
            z-index: 110;
        }
    }

    &__support {
        position: relative;
        display: block;
        width: 100%;
        height: 80px;
        border-top: 1px solid darken($documentation-background, 9%);

        & > .button {
            position: relative;
            display: block;
            width: 180px;
            margin: 0 auto;
            margin-top: 15px;
        }
    }

    &.arkio {
        .documentation__aside {
            background: $documentation-arkio-background;

            &:before {
                background: -webkit-linear-gradient(top,transparent, $documentation-arkio-background);
                background: linear-gradient(to bottom,transparent, $documentation-arkio-background);
            }
        }

        .documentation__support {
            border-top: 1px solid darken($documentation-arkio-background, 9%);
        }

        .documentation__menu > ul li a {
            color: lighten($documentation-arkio-background, 43%);
        }

        .documentation__title-link {
            background: $documentation-arkio-background;
        }

        .documentation__content {
            a {
                color: $documentation-arkio-background;;
                border-bottom-color: $documentation-arkio-background;
            }

            blockquote {
                border-left: 3px solid $documentation-arkio-background;
            }
        }
    }

    &__menu {
        position: relative;
        display: block;
        height: calc(100% - 80px);
        width: 100%;
        overflow: hidden;

        &-top {
            & > .button {
                position: relative;
                display: block;
                width: 190px;
                margin: 40px auto;
            }
        }

        ul {
            list-style: none;
            padding: 0;
            margin: 0;
        }

        & > ul {
            max-width: 266px;
            margin: 0 auto;
            padding: 0 15px;
            margin-bottom: 100px;

            li {
                padding: 0;
                margin: 0;
                list-style: none;

                a {
                    text-decoration: none;
                    color: lighten($documentation-background, 43%);
                    display: block;
                    height: 30px;
                    line-height: 30px;
                    text-overflow: ellipsis;
                    width: 100%;
                    white-space: nowrap;
                    overflow: hidden;
                    transition: all 0.3s ease;
                }
            }

            & > li > a {
                font-weight: 600;
                text-transform: uppercase;
                margin-bottom: 5px;
                color: #fff;
            }

            & > li {
                margin-bottom: 40px;

                &.single {
                    margin-bottom: 10px;
                }
            }

            & > li > ul > li {
                &.active,
                &:hover {
                    a {
                        color: #fff;
                        text-indent: 15px;
                    }
                }
            }
        }
    }

    &__footer {
        height: 80px;
        line-height: 80px;
        text-align: center;
        color: #666;
        border-top: 1px solid #ccc;

        a {
            color: inherit;
            font-size: 14px;
        }
    }

    &__changelog {
        list-style: none;
        margin: 0;
        padding: 40px 0 0 0;
        border-top: 1px solid #e4e4e4;

        li {
            display: block;
            position: relative;
            width: 100%;
            margin: 0;
            padding: 15px;

            &:hover {
                background: #eee;
            }

            a {
                width: 100%;
                display: block;
                position: relative;
                text-decoration: none;
                border-bottom: 0;
                color: #333;
            }

        }

        &-icon {
            position: absolute;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            left: 0;
            top: -7px;
            background: #5ecb0e;
            overflow: hidden;

            svg {
                position: relative;
                width: 30px;
                height: 30px;
                fill: #fff;
                margin-top: 6px;
                margin-left: 5px;
            }
        }

        &-text {
            padding-left: 50px;
        }

        &-date {
            position: absolute;
            right: 0;
            font-size: 14.86px;
            color: #bbbcc7;
            width: 22.5%;
            text-align: right;
            padding-right: 2.5%;
        }
    }

    &__menu-link {
        position: fixed;
        display: block;
        top: 0;
        left: 0;
        background: #000;
        font-size: 10px;
        z-index: 10;
        width: 50px;
        height: 50px;
        padding: 2.1em 1.6em;

        &:hover,
        &:focus {
            background: #000;
        }

        span {
            position: relative;
            display: block;
        }

        span,
        span:before,
        span:after {
            background-color: #fff;
            width: 100%;
            height: 0.2em;
        }

        span:before,
        span:after {
            position: absolute;
            margin-top: -0.6em;
            content: " ";
        }

        span:after {
            margin-top: 0.6em;
        }
    }

    &__feature {
        position: relative;
        margin-bottom: 70px;

        &:hover {
            .documentation__title-link {
                opacity: 1;
            }
        }
    }

    &__title-link {
        display: block;
        position: absolute;
        top: 26px;
        left: -40px;
        height: 30px;
        width: 30px;
        opacity: 0;
        text-align: center;
        text-decoration: none;
        background: $documentation-background;
        border-radius: 50%;
        -webkit-transition: opacity 150ms,background 150ms,color 150ms;
        transition: opacity 150ms,background 150ms,color 150ms;

        svg {
            fill: #fff;
            width: 70%;
            height: 100%;
        }
    }

    &__info {
        display: inline-block;
        padding: 3px 13px;
        margin: 0;
        color: #fff;
        font-size: 14px;
        border-radius: 50px;
        -webkit-transform: translateX(10px) translateY(-4px);
        transform: translateX(10px) translateY(-4px);

        &.added {
            background: #5ecb0e;
        }
    }

    &__content {
        position: relative;
        max-width: 880px;
        margin: 0 auto;
        padding: 60px 40px;

        ol li,
        p {
            font-weight: 300;
            line-height: 1.8;
            font-family: Merriweather,"PT Serif",Georgia,"Times New Roman",serif;
        }

        ol li {
            margin-bottom: 15px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        h1,
        h2,
        h3 {
            display: inline-block;
            position: relative;
            margin-top: 20px;
            margin-bottom: 16px;
            font-weight: 700;
            line-height: 1.4;
            font-family: "Monsterrat", sans-serif;
        }

        code:not(.javascript) {
            color: #616870;
            background: #dfe5eb;
            padding: 0 5px;
            border: 1px solid #d0d9e1;
            border-radius: 2px;
        }

        a {
            color: $documentation-background;
            border-bottom: 1px dotted $documentation-background;
        }

        blockquote {
            color: #888;
            margin: 0;
            padding: 3px 0 3px 15px;
            border-left: 3px solid $documentation-background;
            margin-right: 30px;
        }

        img {
            box-shadow: 0 0 3px #dfdfdf;
            padding: .5em;
            background-color: #fff;
            margin: 20px 0;
            max-width: 100%;
            margin-bottom: .75em;
            display: inline-block;
            vertical-align: middle;
            border: 0;
            -ms-interpolation-mode: bicubic;

            &:hover {
                box-shadow: 0 0 4px #bbbcc7;
            }
        }
    }
}

@media (min-width: 800px) {
    .documentation {
        &__layout {
            padding-left: $documentation-menu-width;
            left: 0;

            &.active .documentation__menu-link {
                left: $documentation-menu-width;
            }
        }

        &__aside {
            left: $documentation-menu-width;
        }

        &__menu-link {
            position: fixed;
            left: $documentation-menu-width;
            display: none;
        }
    }
}

@media (max-width: 48em) {
    .documentation__layout.active {
        position: relative;
        left: $documentation-menu-width;
    }
}
