.navigation {
    position: relative;
    background-color: $color-bg;

    &__container {
        @include display(flex);
        @include align-items(center);
        @include justify-content(space-between);
    }

    &__logo {
        position: relative;
        @include rem(height, 35px);

        &--icon {
            display: none;
        }
    }

    &__menu {
        margin: 0;
        font-weight: 300;
        color: #43525a;
        @include display(flex);
        @include align-items(center);
        @include rem(font-size, 16px);
        @include rem(letter-spacing, 0.6px);
        @include rem(padding, 35px 0);

        ul {
            padding: 0;
            margin: 0;

            li {
                display: inline-block;
                list-style-type: none;
                margin: 1.5rem 2rem;

                a {
                    color: #43525a;
                }

                svg {
                    width: 25px;
                    height: 25px;
                }
            }
        }
    }

    &__group {
        display: inline-block;
        padding: 0 2rem;
        @include rem(border-right, 1px solid #bababa);

        &:last-child {
            border-right: none;
        }

        &--social {
            @include rem(padding, 0 28px);
        }

        &--active {
            font-weight: 700;
            position: relative;
            display: inline-block;

            &:after {
                position: absolute;
                left: 0;
                right: 0;
                bottom: -1rem;
                -webkit-transition: all .3s ease-in-out;
                transition: all .3s ease-in-out;
                height: .2rem;
                content: "";
                background-color: $color-secondary;
                background-image: -webkit-linear-gradient(left,lighten($color-secondary, 13%),$color-secondary);
                background-image: linear-gradient(90deg,lighten($color-secondary, 13%),$color-secondary);
            }
        }
    }

    &__toggle {
        color: #fff;
        font-size: 2rem;
        background: none;
        border: none;
        outline: none;
        width: 4rem;
        height: 4rem;
        padding: 0;
        position: relative;
        z-index: 100000;
        border-radius: 0;
        margin: 0;
        @include transition(all 0.3s ease);

        &__bar {
            width: 3.6rem;
            height: .4rem;
            border-radius: 0;
            display: block;
            background-color: #222;
            position: absolute;
            z-index: 1002;
            @include transition(all 0.4s cubic-bezier(.7,0,.3,1));
            @include transform(translateZ(0));

            &:nth-child(1) {
                @include transform(translateY(-1rem));
            }

            &:nth-child(3) {
                @include transform(translateY(1rem));
            }
        }

        &__pulsar {
            display: block;
            position: absolute;
            bottom: -.5rem;
            right: -.7rem;
            height: 2rem;
            width: 2rem;
            background-color: $color-secondary;
            border-radius: 1rem;
            @include animation(togglePulsar 2s ease-in-out infinite);

            &--1 {
                height: 2rem;
                width: 2rem;
            }

            &--2 {
                opacity: 0;
                height: 1.5rem;
                width: 1.5rem;
                bottom: -.25rem;
                right: -.45rem;
                @include animation-delay(1s);
            }
        }

        &--active {
            .navigation__toggle__bar {
                background-color: #fff;
                
                &:nth-child(1) {
                    @include transform(translateY(0) rotate(135deg));
                    @include rem(font-size, 32px);
                }

                &:nth-child(2) {
                    width: 0;
                }

                &:nth-child(3) {
                    @include transform(translateY(0) rotate(-135deg));
                    @include rem(font-size, 32px);
                }
            }

            .navigation__toggle__pulsar {
                display: none;
            }
        }
    }

    @include breakpoint("medium") {
        &__group {
            display: none;

            &.navigation__group--social,
            &.slide-menu {
                display: inline-block;
            }
        }
    }

    @include breakpoint("xs") {
        &__logo {
            &--full {
                display: none;
            }

            &--icon {
                display: block;
            }
        }
    }
}

@include keyframes(togglePulsar) {
    0% {
        opacity: 0.1;
        @include transform(scale(0.2));
    }

    80% {
        opacity: 0.3;
        @include transform(scale(1.2));
    }

    100% {
        opacity: 0.1;
        @include transform(scale(0.2));
    }
}
