*,
a,
input,
button,
img,
select,
a:focus,
input:focus,
button:focus,
img:focus,
select:focus {
    outline: none;
}

*,
*::after,
*::before {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    box-sizing: border-box;
}

html,
body {
    padding: 0;
    margin: 0;
}

html {
    background: #FFF;
    overflow-x: hidden;
    font-size: 62.5%;
    min-width: 380px;
}

body {
    line-height: 1.5;
    font-family: 'Titillium Web','Helvetica Neue',Helvetica,Arial,sans-serif;
    color: #111;
    height: 100%;
    text-align: left;
    overflow-wrap: break-word;
    word-wrap: break-word;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    -moz-hyphens: auto;
    hyphens: auto;
    background: #fff;
    counter-reset: section;
    @include rem(font-size, 18px);
}

a {
    text-decoration: none;
    color: $color-secondary;

    &:hover,
    &:focus,
    &:visited {
        text-decoration: none;
        color: $color-secondary;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    text-rendering: optimizeLegibility;
    color: #222;
    margin: 1em 0 0.5em;
    line-height: 1.2;
}

$alpha: 63px;
$beta: 45px;
$gamma: 31px;
$delta: 22px;
$epsilon: 16px;
$zeta: 11px;
$sizes: h1 $alpha, h2 $beta, h3 $gamma, h4 $delta, h5 $epsilon h6 $zeta;

@each $size in $sizes {
	#{nth($size, 1)} {
		@include rem((
			font-size: nth($size, 2)
		));
	}
}

::-moz-selection {
    background: $color-secondary;
    color: #fff;
}

::selection {
    background: $color-secondary;
    color: #fff;
} 
