.title {
    position: relative;
    padding: 100px 0 20px 0;
    text-align: center;
    color: $color-bg;

    &__main,
    &__text {
        color: inherit;
    }

    &__main {
        color: $color-extra;
        text-transform: uppercase;
        @include rem(font-size, 48px);
    }

    &__text {
        font-weight: 400;
        line-height: 1;
        @include rem(font-size, 32px);
    }
}
