.footer {
    text-align: left;
    padding: 60px 0 0 0;
    border-top: 1px solid rgba(0,0,0,.1);
    background: #fafafa;

    &__wrapper {
        padding-bottom: 60px;
    }

    &__title {
        text-transform: uppercase;
        font-weight: 600;
        padding-bottom: 20px;
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;

        li {
            margin-bottom: 1rem;

            a {
                color: #666;
            }
        }
    }

    &__social {
        padding-top: 20px;
        border-top: 1px solid rgba(0,0,0,.1);
    }

    &__notice {
        background: $color-primary;
    }

    &__smallprint {
        padding: 20px 0;
        text-align: center;

        p {
            color: #fff;
            margin: 0;
            padding: 0;
            margin-bottom: 0;
            margin-top: 0;
            @include rem(font-size, 12px);
        }
    }
}
