.main {
    position: relative;
    overflow: hidden;
    background: $color-secondary;

    &__post {
        padding: 60px 0 120px 0;
        color: #fff;

        h2 {
            color: inherit;
            @include rem(font-size, 26px);
        }

        &--wrapped {
            max-width: 820px;
            margin: 0 auto;
        }
    }

    @include breakpoint('small') {
        &__post {
            padding: 40px 0 80px 0;
        }
    }
}

.browser {
    max-width: 1000px;
    height: 400px;
    display: block;
    outline: none;
    background: white;
    border-left: 1px solid #ccc;
    border-right: 1px solid #ccc;
    border-bottom: 5px solid #ccc;
    border-radius: 5px 5px 3px 3px;
    margin: 30px auto 0 auto;
    position: relative;

    &__bar {
        width: 100%;
        height: 30px;
        background: linear-gradient(to bottom, #ccc 0%, darken(#ccc, 10%) 100%);
        border-radius: 5px 5px 0 0;
        border-bottom: 1px solid darken(#ccc, 10%);
        border-top: 1px solid lighten(#ccc, 20%);
        position: relative;
        display: flex;
        align-items: center;

        &:after,
        &:before {
            content: '';
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background: lighten(#ccc, 40%);
            position: absolute;
            display: block;
        }

        &:before {
            left: 15px;
            box-shadow: 15px 0 0 0 lighten(#ccc, 40%);
        }

        &:after {
            left: 45px;
        }
    }

    &__content {
        height: 400px - 30px;
        width: 100%;
        display: block;
        position: relative;
        background-size: cover;
        overflow: hidden;
        border-bottom: 5px solid #ccc;
    }
}

.chooser {
    margin-top: 25px;

    &__headline {
        font-size: 56px;
        margin-top: 90px;
        margin-bottom: 10px;
        text-align: center;
    }

    &__center {
        width: 100%;
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__title {
        display: inline-block;
        font-size: 24px;
        padding-bottom: 5px;
        border-bottom: 2px solid #222;
        margin-bottom: 0;
        text-align: center;
    }

    &__button {
        display: block;
        max-width: 320px;
        font-size: 18px;
        margin: 25px auto 0 auto;
    }
}
