#wave-wrap {
      position: relative;
      height: 100px;
      overflow: hidden;
      background: $color-bg;
}

#wave_1,
#wave_2 {
    image-rendering: optimizeSpeed;
    image-rendering: -moz-crisp-edges;
    image-rendering: -webkit-optimize-contrast;
    image-rendering: -o-crisp-edges;
    image-rendering: pixelated;
    -ms-interpolation-mode: nearest-neighbor;
}

#wave_1 {
      outline: none;
      width: 100%;
      position: relative;
      bottom: -5px;
}

#wave_2 {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 2;
      outline: none;
      width: 100%;
}
