.button {
    display: inline-block;
    border: none;
    background: none;
    text-align: center;
    vertical-align: middle;
    position: relative;
    z-index: 1;
    -webkit-backface-visibility: hidden;
    -moz-osx-font-smoothing: grayscale;
    cursor: pointer;
    min-width: 220px;
    max-width: 360px;
    color: #fff;
    text-transform: uppercase;
    font-weight: 700;
    padding: 15px 25px;
    text-decoration: none;
    margin-right: 10px;
    border-radius: 50px;
    background: transparent;
    letter-spacing: 0.5px;
    @include rem(font-size, 22px);

    &.primary {
        color: #FFF;
        background: $color-primary;
    }

    &.secondary {
        color: #FFF;
        background: $color-secondary;
    }

    &.transparent {
        color: #fff;
        background: transparent;
    }

    &--round-s {
        border-radius: 5px;
    }

    &__clean {
        font-weight: 400;
        border: 1px solid #fff;
        border-radius: 40px;
        padding: 13px 35px;
        font-size: 14px;
        transition: color, background 0.3s ease;

        &.active,
        &:hover {
            background: #fff;
            color: #333;
            box-shadow: 0 0 5px rgba(#000, 0.1);
        }
    }
}
