.banner {
    position: relative;
    z-index: 10;
    width: 100%;
    overflow: hidden;
    display: inline-block;
    height: 250px;
    background: $color-bg;
    @include display(flex);
    @include align-items(center);
    @include justify-content(center);
    @include clearfix;

    &__title {
        margin: 0;
        padding: 0;
        color: $color-primary;
        @include rem(font-size, 42px);
    }
}
