.scene_element {
    @include animation-duration(0.25s);
    @include transition-timing-function(ease-in);
    @include animation-fill-mode(both);

    // An element that fades in
    &--fadein {
        @include animation-name(fadeIn);
    }

    // An element that fades in and slides up
    &--fadeinup {
        @include animation-name(fadeInUp);
    }

    // An element that fades in and slides to viewport from right
    &--fadeinright {
        @include animation-name(fadeInRight);
    }

    &--fadeindown {
        @include animation-name(fadeInDown);
    }

    &--fadeinleft {
        @include animation-name(fadeInLeft);
    }
}

@include keyframes (fadeInDown) {
    0% {
        opacity: 0;
        @include transform(translate3d(0, -100%, 0));
    }

    100% {
        opacity: 1;
        @include transform(none);
    }
}

@include keyframes (fadeInLeft) {
    0% {
        opacity: 0;
        @include transform(translate3d(-100%, 0, 0));
    }

    100% {
        opacity: 1;
        @include transform(none);
    }
}

@include keyframes (fadeIn) {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@include keyframes (fadeInUp) {
    0% {
        opacity: 0;
        @include transform(translate3d(0, 100%, 0));
    }

    100% {
        opacity: 1;
        @include transform(none);
    }
}

@include keyframes (fadeInRight) {
    0% {
        opacity: 0;
        @include transform(translate3d(100%, 0, 0));
    }

    100% {
        opacity: 1;
        @include transform(none);
    }
}
