.menu {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background: $color-secondary;
    padding: 3.125em;
    left: -100%;
    transition: all 0.75s cubic-bezier(0.75, 0.19, 0.24, 1);
    z-index: 100;

    &--active {
        left: 0;
    }

    a {
        color: #fff;
    }

    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        margin-top: 1.875em;
        transform: translateX(0);
        transition: 0.7s 0.65s;

        li {
            cursor: pointer;
            color: #fff;
            font-weight: 200;
            line-height: 1.3;
            transition: 0.3s;
            @include rem(font-size, 42px);

            a {
                @include transition(color 300ms ease);
            }

            &.active,
            &:hover {
                a {
                    font-weight: 700;
                    color: $color-extra;
                }
            }

            @include breakpoint("xs") {
                @include rem(font-size, 20px);
            }
        }
    }

    &__contact {
        position: absolute;
        bottom: 3.125em;
        left: 3.125em;
        @include transform(translateX(0));

        a {
            display: block;
            text-decoration: none;
            color: #fff;
            line-height: 1.2;
            font-weight: 700;
            @include rem(font-size, 32px);
            @include transition(all 300ms ease);

            &:hover {
                color: $color-extra;
                @include transform(scale(1.1));
            }
        }
    }
}
