.intro {
    height: 77vh;
    width: 100%;
    position: relative;
    overflow: hidden;
    background-color: $color-bg;

    &__wrapper {
        height: 100%;
        max-width: 1200px;
        margin: 0 auto;
        @include display(flex);
        @include align-items(center);
        @include justify-content(center);
    }

    &__title {
        font-weight: 700;
        color: $color-primary;
        text-transform: uppercase;
        margin: 0 0 10px 0;
        padding: 0;
        @include rem(font-size, 52px);
    }

    &__subtitle {
        margin: 0;
        padding: 0;
        color: #323232;
        @include rem(font-size, 32px);
    }

    &__imageholder {
        position: relative;
        width: 50%;

        img {
            position: absolute;
            width: 170%;
            @include transform(translateY(-50%));
        }
    }

    &__text {
        width: 50%;
        z-index: 30;
        padding: 0 15px;
    }

    &__download {
        margin-top: 40px;
        z-index: 30;
        @include display(flex);
        @include flex-wrap(wrap);
    }

    @include breakpoint('large') {
        &__text {
            width: 80%;
            padding-left: 70px;
        }
    }

    @include breakpoint('medium') {
        &__text {
            width: 90%;
        }

        &__imageholder {
            img {
                left: -50px;
            }
        }

        &__title {
            @include rem(font-size, 44px);
        }

        &__subtitle {
            @include rem(font-size, 26px);
        }

        &__download {
            .button {
                width: 190px;
                padding: 10px 0;
            }
        }
    }

    @include breakpoint('small') {
        &__wrapper {
            @include flex-direction(column);
        }

        &__text {
            text-align: center;
            width: 100%;
            padding: 0 15px;
        }

        &__download {
            margin: 30px 0;
            @include justify-content(center);
        }

        &__imageholder {
            width: 100%;
            height: 30vh;
            padding: 0;

            img {
                display: block;
                position: relative;
                width: 90%;
                left: auto;
                margin: 0 auto;
                @include transform(translateY(0px));
            }
        }
    }

    @include breakpoint('xs') {
        &__imageholder {
            display: none;
        }

        &__download {
            a {
                margin-top: 15px;

                &:first-child {
                    margin-top: 0;
                }
            }
        }
    }
}
